import React from "react";
import Container from "../components/Container"
import myFace from "../images/me.jpg"
import "./style.css"

function about(){
    return(
        <div>
            <Container main={
                    <>
                        <h1> Christopher Watkins, Web Dev </h1>
                        <img className={`about-img`} src={myFace}/>
                    
                    </>



            }
            
            
            
            /> 
                      
            

        </div>
    )
} 

export default about;